#header {
    height: 100vh;
    padding-top: 75px;
}

.header-container {
    text-align: center;
    height: 100%;
    position: relative;
}

.header-text-container {
    justify-content: center;
    padding-top: 20vh;
}

/* cta buttons */
.cta {
    margin-top: 50px;
    display: flex;
    gap: 1rem;
    justify-content: center;
}

/* socials icons */
.header-socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 50px;
    font-size: 18px;
}

.header-socials::after {
    content: "";
    width: 1px;
    height: 80px;
    background: var(--color-primary);
}


/* down arrow */
.scroll-down {
    position: absolute;
    right: -5px;
    bottom: 50px;
    font-weight: 300;
    font-size: 20px;
}

/* media queries */
@media screen and (max-width: 1024px) {
    header {
        height: 68vh;
    }
}

@media screen and (max-width: 600px) {
    header {
        height: 100vh;
    }

    .scroll-down {
        display: none;
    }
    .header-socials {
        flex-direction: row;
        bottom: 10vh;
        left: 35vw;
    }
    .header-socials::after {
        display: none;
    }
}